






















export default {
	data:{
		orderList:[],
		'uid':''
	},
	methods:{
		getOrderList:function(){
			var _this = this;
			_this.uid = uni.getStorageSync('uid');
			wx.request({
				url: 'https://www.slluks.com/index.php/app/order/orderlist',
				data: {
					'uid' : _this.uid,
					'order_status' : '',
				},
				method:'POST',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success: function(res) {
					_this.orderList = [];
					res.data.forEach(
						item => {
							_this.orderList.push(item);
						}
					);
					uni.showToast({
						title: '加载成功',
						duration: 2000,
						icon: 'none'
					});
					console.log(res.data);
				},
				fail: function(){
					uni.showToast({
						title: '加载失败',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		}
	},
	onLoad() {
		this.getOrderList();
	},
	onPullDownRefresh() {
		
	},
	onReachBottom() {
		
	},
	onTabItemTap(){
		
	}
}
